import React, { PureComponent } from "react";
import { createStateLink, StateInf, StateLink } from '@hookstate/core';
import { ProcessEditor } from "./process.editor";
import { ProcessNode, ProcessNodeConnector } from "~/components";

export class ProcessScreen extends PureComponent<any> {
    processNodesSL = createStateLink<ProcessNode[]>([
        { id: 1, type: 1, desc: "Node 1", pos: { x: 100, y: 200 }, outputs: [{ target: { idTargetNode: 2, idTargetNodeParameter: 0 }}]},
        { id: 2, type: 1, desc: "Node 2", pos: { x: 300, y: 200 }, inputs: [{}], outputs: [{ target: { idTargetNode: 3, idTargetNodeParameter: 0 }, value: "True"}, { target: { idTargetNode: 4, idTargetNodeParameter: 0 }, value: "False"}]},
        { id: 3, type: 1, desc: "Node 3.1", pos: { x: 500, y: 150 }, inputs: [{}]},
        { id: 4, type: 1, desc: "Node 3.2", pos: { x: 500, y: 350 }, inputs: [{}]}
    ]);

    processNodeConnectorsSL = createStateLink<ProcessNodeConnector[]>([]);    

    render() {
        return (
            <ProcessEditor navigation={ this.props.navigation } processNodesSL={ this.processNodesSL } processNodeConnectorsSL= { this.processNodeConnectorsSL } />
        );
    }
}