import React from "react";
import { View, Text, PanResponder } from "react-native";
import { ProcessNodeDisplay } from ".";
import { useStateLink } from "@hookstate/core";
import Svg, { Line, Path } from "react-native-svg";
import { ProcessNode, ProcessNodeConnector } from "~/components";

export const ProcessEditor = (props) => {
    const nodes = useStateLink<ProcessNode[]>(props.processNodesSL);
    const connectors = useStateLink<ProcessNodeConnector[]>(props.processNodeConnectorsSL);

    const [position, setState] = React.useState({ x: 0, y: 0 });
    const lastPosition = React.useRef({ x: 0, y: 0 }).current;
    let pressCount = 0;
    const panResponder = React.useMemo(() => PanResponder.create({
        onStartShouldSetPanResponder: (evt, gestureState) => true,
        onPanResponderGrant: (evt, gestureState) => {
            pressCount += 1;
            setTimeout(() => {
                pressCount = 0;
            }, 500)
        },
        onPanResponderMove: (evt, gestureState) => {
            setState({ x: lastPosition.x + gestureState.dx, y: lastPosition.y +  gestureState.dy });
        },
        onPanResponderRelease: (evt, gestureState) => {
            lastPosition.x += gestureState.dx;
            lastPosition.y += gestureState.dy;
            if (pressCount === 2) {
                props.navigation.navigate({
                    routeName: 'Modal'
                });
            }
        },
        
    }), []);

    return (
        <View style={{ flex: 1, flexDirection: "row", overflow: "hidden", backgroundColor: "#999999" }} {...panResponder.panHandlers} > 
            <View style={{ flex: 1, zIndex: -1, transform: ([ { translateX: position.x }, { translateY: position.y }]) }} >
                { nodes.value && nodes.value.map(n => 
                    <ProcessNodeDisplay id={ n.id } {...n } processNodeConnectorsSL={ props.processNodeConnectorsSL } />
                )}
                <Svg height="100%" width="100%" style={{ zIndex: 2 }} >
                    { connectors.value && connectors.value.map(c => 
                        connectors.value.filter(cc => !c.in && cc.in && c.idTargetNode == cc.idNode && c.idTargetNodeParameter == cc.idParameter).map(cc => 
                            <Line x1={ c.position.x + 10 } y1={ c.position.y + 10 } x2={ cc.position.x + 10 } y2={ cc.position.y + 10 } stroke="red" strokeWidth="1" />
                        )
                    )}
                </Svg>
            </View>   
        </View>
    );
}