import React from "react";
import { View, Text, PanResponder, StyleSheet } from "react-native";
import { StateInf, StateLink, useStateLink } from "@hookstate/core";
import { Entypo } from "@expo/vector-icons";
import { InParameter, OutValue, ProcessNode, ProcessNodeConnector } from "~/components";

const ProcessNodeDisplayStyle = StyleSheet.create({
    container: {
		position: "absolute",
        zIndex: 1,
        alignSelf: "flex-start",
        width: 150,
        padding: 5,
        backgroundColor: "white"
    },
    connector: {
        width: 20,
        height: 20,
        borderRadius: 20/2
    },
	inContent: {
		flexDirection: "row",
        alignSelf: "flex-start"
    },
    inConnector: {
        backgroundColor: "green"
    },
	outContent: {
		flexDirection: "row",
        alignSelf: "flex-end",
    },
    outConnector: {
        backgroundColor: "blue"
    }
})

export interface ProcessNodeDisplayProps extends ProcessNode {
    processNodeConnectorsSL: StateInf<StateLink<ProcessNodeConnector[]>>;
}

export interface ProcessNodeDisplayInParameterProps extends InParameter {
    idNode: number,
    idParameter: number,
    processNodeConnectorsSL: StateInf<StateLink<ProcessNodeConnector[]>>;
}

export interface ProcessNodeDisplayOutValueProps extends OutValue {
    idNode: number,
    idParameter: number,
    processNodeConnectorsSL: StateInf<StateLink<ProcessNodeConnector[]>>;
}

const ProcessNodeDisplayInParameter = (props: ProcessNodeDisplayInParameterProps) => {
    return (
        <View style={ ProcessNodeDisplayStyle.inContent }>
            <ProcessNodeDisplayConnector
                direction="in"                
                idNode={ props.idNode }
                idParameter={ props.idParameter }
                idTargetNode={ null }
                idTargetNodeParameter={ null }
                processNodeConnectorsSL={ props.processNodeConnectorsSL }
            />
            { props.value && 
                <Text>{ props.value }</Text>
            }
        </View>
    );
}

const ProcessNodeDisplayOutValue = (props: ProcessNodeDisplayOutValueProps) => {
    return (
        <View style={ ProcessNodeDisplayStyle.outContent }>
            { props.value && 
                <Text>{ props.value }</Text>
            }
            <ProcessNodeDisplayConnector
                direction="out"
                idNode={ props.idNode }
                idParameter={ props.idParameter }
                idTargetNode={ props.target.idTargetNode }
                idTargetNodeParameter={ props.target.idTargetNodeParameter }
                processNodeConnectorsSL={ props.processNodeConnectorsSL }
            />
        </View>
    );
}

const ProcessNodeDisplayConnector = (props) => {    
    let connectorsSL = useStateLink<ProcessNodeConnector[]>(props.processNodeConnectorsSL);
    let marker;

    const layoutCb = (evt) => {
        if (marker) {            
            marker.measure((x, y, width, height, pageX, pageY) => {
                connectorsSL.set(s => {
                    let isIn = props.direction == "in"
                    const conns = s.filter(c => isIn && c.idNode == props.idNode && c.idParameter == props.idParameter);        
                    if (conns.length > 0) {
                        conns[0].position = { x: pageX, y: pageY };
                    }
                    else {
                        s.push({
                            idNode: props.idNode,
                            idParameter: props.idParameter,
                            in: isIn,
                            idTargetNode: props.idTargetNode,
                            idTargetNodeParameter: props.idTargetNodeParameter,
                            position: { x: pageX, y: pageY },
                            size: { x: 20, y: 20 }            
                        })
                    }
                    return s;
                });
                
            });
        }
    }

    const connectorDir = props.direction == "in" ? "green" : "blue";

    return (
        <View ref={ref => { marker = ref }} onLayout={ layoutCb }>
            <Entypo name="arrow-right" size={18} color={ connectorDir } />
        </View>
    )
}

export const ProcessNodeDisplay = (props: ProcessNodeDisplayProps) => {
    const panResponder = React.useMemo(() => PanResponder.create({
        onStartShouldSetPanResponder: (evt, gestureState) => true,
        onPanResponderMove: (evt, gestureState) => {
        }
    }), []);

    return (
        <View style={ [ProcessNodeDisplayStyle.container, { top: props.pos.y, left: props.pos.x }] } {...panResponder.panHandlers}>
            <View>
                <Text>
                    { props.desc }
                </Text>
            </View>
            <View style={{ flexDirection: "row" }}>
				<View style={{ flex: 1 }}>
                    { props.inputs && props.inputs.map((i, idx) => 
                        <ProcessNodeDisplayInParameter { ...i } idNode={ props.id } idParameter={ idx } processNodeConnectorsSL={ props.processNodeConnectorsSL } />
                    )}
				</View>
				<View style={{ flex: 1 }}>
                    { props.outputs && props.outputs.map((o, idx) =>
                        <ProcessNodeDisplayOutValue { ...o } idNode={ props.id } idParameter={ idx } processNodeConnectorsSL={ props.processNodeConnectorsSL } />
                    )}
				</View>
            </View>
        </View>
    )
}